import React, { useState } from "react";
import LinearProgress from "@material-ui/core/LinearProgress";
import Button from "@material-ui/core/Button";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import { ImShare2 } from "react-icons/im";


var numeral = require("numeral");

const FeatureProjectCards = ({ history }) => {
  const [featuredPage, setFeaturedPage] = useState(0);
  const [featuredCardPerPage] = useState(9);
  const indexOfLastCard = featuredPage * featuredCardPerPage;
  const indexOfFirstCard = indexOfLastCard + featuredCardPerPage;

  const featuredProjectState = useSelector(state => state.projectsReducer)
  const { allProjects } = featuredProjectState

  return (
    <>
     {allProjects.length > 0 ? allProjects.slice(indexOfLastCard, indexOfFirstCard).map(project =>{
        const { 
          project_name,
          user,
          id,
          project_img,
          project_amount_raised,
          project_percentage_completed,
          project_endAt,
          project_description,
          project_cash_amount } = project 
          const { first_name, last_name } = user;
          const percentageCompleted = Number(project_percentage_completed).toFixed(
            1
          );
          let projectAmount = numeral(project_cash_amount).format("0, 0");      
          let amountRaised = numeral(project_amount_raised).format("0, 0");      
      
    return(
      <div className="col-sm-6 col-md-6 col-lg-4 p-t-10">
        <div className="card" >
          <img
            className="card-img-top"
            src={project_img}
            alt="imageCard"
            onClick={() => {
              window.scrollTo(
                {
                  top: 0,
                  behavior: "smooth"
                },
                history.push(`/project/${id}/details`)
              );
            }}
          />

          <div className="card-body">
            <div>
              <div className="d-flex justify-content-between">
                <div className="d-flex">
                  <div
                    className="ai-outline"
                    title="share"
                    onClick={() => {
                      window.scrollTo(
                        {
                          top: 0,
                          behavior: "smooth"
                        },
                        history.push(`/project/${id}/details`)
                      );
                    }}
                  >
                    
                  </div>
                </div>
              </div>
              <div className="d-flex justify-content-between">

              <h4
                className="card-title pt-2 link-router truncate"
                onClick={() => {
                  window.scrollTo(
                    {
                      top: 0,
                      behavior: "smooth"
                    },
                    history.push(`/project/${id}/details`)
                  );
                }}
              >
               {project_name}
                    </h4>
                    <div
                      className="ai-outline"
                      title="share"
                      onClick={() => {
                        window.scrollTo(
                          {
                            top: 0,
                            behavior: "smooth"
                          },
                          history.push(`/project/${id}/details`)
                        );
                      }}
                    >
                      <ImShare2 color="#C75A00" />
                    </div>
              </div>

              <LinearProgress
                value={percentageCompleted}
                variant="determinate"
              />

              <div className="mt-1 mb-3 truncate">{project_description}</div>

              <div>
                <div className="row justify-content-between mt-2 contributed-progress-view">
                  <p className="contributed-amount m-l-15 font-weight-bold">
                  ₦{amountRaised}
                  </p>
                  <span className="m-r-12 font-weight-bold">
                      {percentageCompleted + "%"}
                  </span>
                </div>
                <p className="pt-0">
                  raised of ₦{projectAmount}
                </p>
              </div>

              <div className="mt-5 card-donate-btn-container">
                <Button
                  variant="contained"
                  className="card-donate-btn"
                  onClick={() => {
                    window.scrollTo(
                      {
                        top: 0,
                        behavior: "smooth"
                      },
                      history.push(`/project/${id}/details`)
                    );
                  }}
                 >
                  Donate
              </Button>
              </div>

              <hr />
              <div className="row donated-ws">
                <p className="m-l-15">
                  by {first_name + " " + last_name.substring(0, 1)}
                      </p>
                <span className="m-r-12">End date : {project_endAt}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
         )
        }) :     
       <div className="d-flex justify-content-center align-items-center w-100">
        <p>No Fund Available. Create a new Fund</p>
      </div>
       }
    </>
  );
};

export default withRouter(FeatureProjectCards);
