import React from "react";
import ProjectItems from "../subpages/project/ProjectItems";
import Layout from "util/Layout";



const ProjectCollections = () => {
  return (
    <Layout>
      <div className="container">
        <h1 className="text-capitalize d-flex justify-content-center mt-5">Projects</h1>
        <div className="row">
          <ProjectItems />
        </div>
      </div>
    </Layout>
  );
};

export default ProjectCollections;
