import React, { useState } from "react";
import { Link } from "react-router-dom";
import Button from "@material-ui/core/Button";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
var numeral = require("numeral");

const AuctionCard = ({ history }) => {
  const [featuredPage, setFeaturedPage] = useState(0);
  const [featuredCardPerPage] = useState(3);
  const indexOfLastCard = featuredPage * featuredCardPerPage;
  const indexOfFirstCard = indexOfLastCard + featuredCardPerPage;
  const auctionProduct = useSelector((state) => state.auctionReducer.auctionCollections);

  return (
    auctionProduct.map(auction =>{
      const {
        id,
        auction_item_name,
        auction_item_img,
        auction_endAt
      } = auction;
      return(
        <>

      <div className="col-md-6 col-lg-3 my-3">
        <div className="card auction-card">
          <div className="img-container">
            <img
              src={auction_item_img}
              alt="product"
              className="card-img-top mb-3"
              onClick={() =>
                {window.scrollTo(
                  {
                    top: 0,
                    behavior: "smooth",
                  }
                  )
                history.push(`auction/${id}/details`)}
              }
            />

            <div className="bid-btn-container">
              <div className="bid-btn-subdiv">
                <Button
                  className="card-text bid_btn"
                  onClick={() =>
                    {window.scrollTo(
                      {
                        top: 0,
                        behavior: "smooth",
                      }
                      )
                    history.push(`auction/${id}/details`)}
                  }
                >
                  Bid Now
                </Button>
              </div>
            </div>
          </div>

          <p className="mx-auto truncate text-uppercase mt-3 mb-0  bid-item-name">
            {auction_item_name}
          </p>

          {/*card footer */}
          <div className="auction-starter d-flex flex-column align-items-center justify-content-center mt-4">
            <h6 className="font-italic mb-0 ">
              Starting-bid:
              <span className="font-weight-bold ml-2">
                {/* {donate_currency}
                {bidMinValue} */}
              </span>
            </h6>
            <h6 className="font-italic mt-2 mb-0">
              Expires on:
              <span className="font-weight-bold ml-2">{auction_endAt}</span>
            </h6>
          </div>
        </div>
      </div>
    </>
      )
    })
  );
};

export default withRouter(AuctionCard);
