import React from 'react'
import AuctionItems from '../subpages/auction/AuctionItems';
import Layout  from 'util/Layout';


const AuctionCollections = () => {
    return (
      <Layout>
        <h1 className="text-capitalize d-flex justify-content-center mt-5">Auction Products</h1> 
        <div className="container"> 
         <div className="row">  
           <AuctionItems />
        </div>
       </div> 
      </Layout>    
    )
}

export default AuctionCollections
