import React, { useState } from "react";
import LinearProgress from "@material-ui/core/LinearProgress";
import Button from "@material-ui/core/Button";
import { AiOutlineFolderOpen } from "react-icons/ai";
import Avatar from "@material-ui/core/Avatar";
import { FiMail } from "react-icons/fi";
import SocialMediaButtons from "components/ui/ShareSocialLinks/SocialMediaButtons";
import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import CopyShared from "components/ui/ShareSocialLinks/CopySharedLink";
import { useSelector } from "react-redux";
import { Link, withRouter } from "react-router-dom";


var numeral = require("numeral")

const SingleDonateCards = ({ shareUrl, className, setIsDonateCardButtonsOpen, history }) => {
const [modal, setModal] = useState(false);
const toggle = () => setModal(!modal);

  const singlePro= useSelector(state => state.projectsReducer.singleProject);

  // const "isAuthenticated" = useSelector(state => state.authReducer."isAuthenticated");
  
  const {
    project_name,project_createdAt, project_cash_amount,
                  project_endAt, user,project_percentage_completed,project_amount_raised
  } = singlePro;


  const fundAmount = numeral(project_cash_amount).format("0, 0");
  const fundCashAmount = numeral(project_amount_raised).format("0, 0");
  const percentageCompleted = Number(project_percentage_completed).toFixed(1);


  return (
    <>
      <div className="card">
        <div className="card-body">
          <div>
            <Link className="link-router">
              <h4 className="card-title">{project_name}</h4>
            </Link>
            <div className="donors-info m-b-25">
              <Avatar src={user} />
              <div className="donor-name m-l-9">
                <span>
                  <FiMail />
                </span>

                <div className="text-muted">{project_createdAt}</div>
              </div>
            </div>

            <LinearProgress
              value={project_percentage_completed}
              variant="determinate"
            />
            <div className="row justify-content-between mt-3 contributed-progress-view">
              <p className="contributed-amount m-l-15 font-weight-bold">
                {"₦" + fundAmount}
              </p>
              <span className="font-weight-bold">
                {percentageCompleted + "%"}
              </span>
            </div>
            <p className="text-muted">
              raised of {"₦" + fundCashAmount}
            </p>
            <div
              className="card-donate-btn-container mt-3"
              id="donate-id-button"
            >
              <Button
                variant="contained"
                className="card-campaign-btn"
                id="DoncardBtn"
                onClick={() => {
                  "isAuthenticated"
                    ? setIsDonateCardButtonsOpen(true)
                    : history.push("/auth");
                }}
              >
                Donate
              </Button>
            </div>
            <div className="card-donate-btn-container mt-3">
              <Button
                variant="contained"
                className="card-share-btn"
                onClick={toggle}
              >
                Share
              </Button>
            </div>

            <hr />
            <div className="row donated-ws">
              {/* {user.length>=1?<p className="m-l-15">by {user}</p>:null} */}
              <span className="m-r-12">End Date: {project_endAt}</span>
            </div>
          </div>
        </div>

        {/* modal   */}
        <Modal
          isOpen={modal}
          toggle={toggle}
          className={className}
          id="share-modal"
        >
          <ModalHeader toggle={toggle}>Share on</ModalHeader>
          <ModalBody>
            <SocialMediaButtons shareUrl={shareUrl} />
          </ModalBody>
          <ModalFooter>
            <CopyShared shareUrl={shareUrl} />
          </ModalFooter>
        </Modal>
      </div>
    </>
  );
};

export default withRouter(SingleDonateCards);
