import React from "react";
import { Button } from "@material-ui/core";
import { FaArrowRight } from "react-icons/fa";
import { Link } from "react-router-dom";
import FeaturedAuctionCards from "./ui/FeaturedAuctionCards";


const FeaturedAuctionItems = () => {
  return (
    <div className='container'>
      <div className='row'>
      <FeaturedAuctionCards />
      <div className="more-market-support">
        <Link className="link-router" to="/auction-products">
          <Button
            className="text-capitalize market-place-btn mt--10"
            onClick={() => window.scrollTo({ top: 0, behavior: "smooth" })}
             >
            More <FaArrowRight style={{ color: "#fff", marginLeft: "10px" }} />
          </Button>
        </Link>
      </div>
      </div>
    </div>
  );
};

export default FeaturedAuctionItems;
