import * as actionTypes from "store/actions/ActionTypes";

const initialState = {
    createProject: [],
    allProjects: [],
    singleProject: [],
    paystackUrl: "",
    submitted: false,
};

const Project = (state = initialState, action) => {
    const { type, payload } = action;

    switch (type) {
        case actionTypes.FETCH_ALL_PROJECT_SUCCESS:
            return { ...state, allProjects: payload }
        case actionTypes.FETCH_SINGLE_PROJECT_SUCCESS :
            return { ...state, singleProject: payload }
        case actionTypes.CREATE_PROJECT_SUCCESS:
            return {
                ...state,
                createProject: payload,
       };
        case actionTypes.CREATE_DONATION_CASH_SUCCESS:
            return {
                ...state,
                paystackUrl: payload,
       };
        case actionTypes.FETCH_ALL_PROJECT_FAIL:
        case actionTypes.CREATE_PROJECT_FAIL:
        case actionTypes.FETCH_SINGLE_PROJECT_FAIL:
            return {
                ...state,
                loading: true
            }
        default:
            return state;
    }
};

export default Project;