import React from "react";
import FeaturedAuctionItems from './../auction/FeaturedAuctionItems';


const FeaturedAuction = () => {
    return (
        <>
            <div className="feature-headline text-center">
                <h1 className="text-capitalize text-center">Featured Auction</h1>
                <p>Auction Raised</p>
                <FeaturedAuctionItems />
            </div>
        </>
    );
};

export default FeaturedAuction;
