import * as actionTypes from "./ActionTypes"
import axiosInstance  from 'util/api'
import { setLoading, offLoading } from 'store/actions/Common';
import { tokenConfig } from 'util/TokenConfig';
import {stopSubmit, reset} from "redux-form"


//  CREATE AUCTION PRODUCT
export const auctionProduct = (Values) => async(dispatch, getState) => {
  dispatch(setLoading());
try {
  const res= await axiosInstance.post("auction/create", {...Values}, tokenConfig(getState))
  dispatch({type: actionTypes.CREATE_AUCTION_SUCCESS});
  dispatch({type: actionTypes.SHOW_SUCCESS_MESSAGE, payload:"Auction Item Created"});
  dispatch(offLoading());
} catch (error) {
  dispatch(setLoading());
  dispatch({type: actionTypes.CREATE_AUCTION_FAIL});
  dispatch(offLoading());
}
}

//  CREATE AUCTION BID+
export const auctionBid = (Values) => async(dispatch,getState) =>{
  dispatch(setLoading());
try {
  const res= await axiosInstance.post("auction/bid_now", {...Values}, tokenConfig(getState))
  dispatch({type: actionTypes.CREATE_AUCTION_SUCCESS});
  dispatch(stopSubmit("auctionBidForm"));
  dispatch(reset("auctionBidForm"));
  dispatch({type: actionTypes.SHOW_SUCCESS_MESSAGE, payload:"Auction Bid Successful"});
  dispatch(offLoading());
} catch (error) {
  dispatch({type: actionTypes.CREATE_AUCTION_FAIL});
  dispatch(offLoading());
  dispatch({type: actionTypes.SHOW_ERROR_MESSAGE, payload:"Auction Bid Fail"});
}
}

// FETCH AUCTION PRODUCTS
export const fetchAuctionProduct = () => async(dispatch) => {
try {
  const res = await axiosInstance.get("auction/list")
  dispatch({type: actionTypes.FETCH_AUCTION_SUCCESS, payload: res.data});
} catch (error) {
  dispatch({type: actionTypes.FETCH_AUCTION_FAIL,payload: error});
}
}
export const fetchSingleAuctionProduct = (id) => async(dispatch) => {
try {
  const res = await axiosInstance.get(`auction/list/${id}`)
  dispatch({type: actionTypes.FETCH_SINGLE_AUCTION_SUCCESS, payload: res.data});
} catch (error) {
  dispatch({type: actionTypes.FETCH_SINGLE_AUCTION_FAIL,payload: error});
}
}

// FETCH AUCTION BIDS
export const fetchAuctionBid = () => async(dispatch, getState) =>{
try {
  const res = await axiosInstance.get("auction/bid", tokenConfig(getState))
  dispatch({type: actionTypes.FETCH_AUCTION_SUCCESS, payload: res.data});
} catch (error) {
  dispatch({type: actionTypes.FETCH_AUCTION_FAIL, payload: error});
}
}