import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { reduxForm, Field } from "redux-form";
import { Button } from "@material-ui/core";
import { renderField } from "util/RenderValidate";
import { auctionBid } from "store/actions/Auction";
import { useDispatch } from "react-redux";
const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    display: "block",
    backgroundColor: theme.palette.background.paper,
    border: "2px solid rgb(199, 90, 0)",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    width: "60%",
    height: "70%",
    borderTopLeftRadius: "10px",
    borderTopRightRadius: "10px",
    borderBottomLeftRadius: "10px",
    borderBottomRightRadius: "10px",
  },
}));

const AuctionModal = ({ handleClose, open, handleSubmit, pristine, submitting, auction_id }) => {
  const [staff, setStaff] = React.useState(false);
  const [Anonymously, setAnonymously] = React.useState(false);
  const [bidAttestation, setBidAttestation] = React.useState(false);
  const dispatch = useDispatch();

  const onFormSubmit = (formValues) => {
    const dataForm = {
      bider_first_name: formValues.bider_first_name,
      bider_last_name: formValues.bider_last_name,
      bider_email: formValues.bider_email,
      bider_phone: formValues.bider_phone,
      bider_address: formValues.bider_address,
      bid_amount: formValues.bid_amount,
      company_designation: formValues.company_designation,
      company_staff: staff,
      bid_as_unknown: Anonymously,
      bid_attestation: bidAttestation,
      auction: auction_id,
    };
    dispatch(auctionBid(dataForm));
  };
  const classes = useStyles();

  const ToggleSwitch = ({ checked, onChange, id }) => (
    <div className="form-group focused">
      <input
        type="checkbox"
        checked={checked}
        onChange={(e) => onChange(e.target.checked)}
        id={id}
      />
    </div>
  );
  return (
    <div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 300,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <div id="transition-modal-title" className="d-flex justify-content-between text-center">
            <h2 className="text-center">
              Auction Form
            </h2>
            <Button  onClick={handleClose}>
        close
      </Button >
            </div>
            
           
            <hr className="profile_hr my-4" />
            <form onSubmit={handleSubmit(onFormSubmit)}>
              <div className="pl-lg-4">
                <div className="row">
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="input-bider_first_name">
                        first Name <span style={{ color: "red" }}>*</span>
                      </label>
                      <Field
                        component={renderField}
                        name="bider_first_name"
                        type="text"
                        id="input-phone"
                        className="form-control form-control-alternative"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="input-bider_last_name">
                        Last Name <span style={{ color: "red" }}>*</span>
                      </label>
                      <Field
                        component={renderField}
                        type="text"
                        id="input-bider_last_name"
                        className="form-control form-control-alternative"
                        name="bider_last_name"
                      />
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="input-phone">
                        Phone <span style={{ color: "red" }}>*</span>
                      </label>
                      <Field
                        component={renderField}
                        name="bider_phone"
                        type="text"
                        id="input-phone"
                        className="form-control form-control-alternative"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="input-email">
                        Email <span style={{ color: "red" }}>*</span>
                      </label>
                      <Field
                        component={renderField}
                        type="text"
                        id="input-email"
                        className="form-control form-control-alternative"
                        name="bider_email"
                      />
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="bider_address">
                        Address <span style={{ color: "red" }}>*</span>
                      </label>
                      <Field
                        name="bider_address"
                        component={renderField}
                        type="text"
                        id="bider_address"
                        className="form-control form-control-alternative"
                      />
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="bid_amount">
                        Amount <span style={{ color: "red" }}>*</span>
                      </label>
                      <Field
                        name="bid_amount"
                        component={renderField}
                        type="text"
                        className="form-control form-control-alternative"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="input-gender">
                        Bid Anonymously?
                      </label>
                      <div>
                        <ToggleSwitch
                          checked={Anonymously}
                          id="bidform"
                          onChange={setAnonymously}
                        />
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-lg-6">
                    <div className="form-group focused">
                      <label className="profile-control-label" for="bid_as_unknown">
                        Are you a member of the origanization ?
                      </label>
                      <div>
                        <div className="form-group focused">
                          <ToggleSwitch checked={staff} id="staffform" onChange={setStaff} />
                        </div>
                      </div>
                    </div>
                  </div>
                  {staff && (
                  <div className="col-md-6">
                      <div className="form-group focused">
                        <label className="profile-control-label" for="company_designation">
                          Designation
                        </label>
                        <Field
                          name="company_designation"
                          component={renderField}
                          type="text"
                          className="form-control form-control-alternative"
                        />
                      </div>
                    </div>
                  )}
                </div>
                <hr className="profile_hr my-4" />
                <div className="pl-lg-4">
                <div className="row d-flex justify-content-between">
                  <div className="col-lg-4">
                    <div className="form-group focused">
                      <div className="d-flex justify-content-around">
                        <ToggleSwitch
                          checked={bidAttestation}
                          id="bid_attestation"
                          onChange={setBidAttestation}
                        />
                      <p className="profile-control-label">
                        Accept bid text shoild be here<span style={{ color: "red" }}>*</span>
                      </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-lg-4">

                  <Button
                      type="submit"
                      style={{ backgroundColor: "#C75A00", color: "#fff" }}
                      disabled={pristine || submitting}
                      onClick={() => {
                        setTimeout(() => {
                          handleClose();
                        }, 2000);
                      }}
                    >
                      Submit
                    </Button>
                  </div>
                </div>
                </div>
              </div>
            </form>
          </div>
        </Fade>
      </Modal>
    </div>
  );
};

export default reduxForm({
  form: "auctionBidForm",
})(AuctionModal);
