import React, { useState } from "react";
import { Field, reduxForm } from "redux-form";
import { useDispatch, useSelector } from "react-redux";
import { login } from "store/actions/Auth";
import { Redirect } from "react-router-dom";
import { renderField, validate } from "util/RenderValidate";
import { BiShow, BiKey, BiHide } from "react-icons/bi";
import {FaUser} from "react-icons/fa"


const LoginForm = ({ handleSubmit, submitting, pristine }) => {
  const dispatch = useDispatch();
  const [isPasswordShown, setIsPasswordShown] = useState(false);

  const authState = useSelector(state => state.authReducer);
  const { isAuthenticated } = authState;

  const onSubmit = formValues => {
    dispatch(login(formValues));
  };

  if (isAuthenticated) {
    return <Redirect to="/dashboard" />;
  }

  const handleShowPassword = () => {
    setIsPasswordShown(!isPasswordShown);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <>
        <div className="login-container">
            <h4 className="login-title">Log in</h4> 
              <Field
                component={renderField}
                label="Email"
                name="email"
                type="email"
                className='login-input'
                autofocus
            />
            

              <Field
                type={isPasswordShown ? "text" : "password"}
                name="password"
                component={renderField}
                label="password"
                className='signup-input'
              />
     
          <button className="login-button" disabled={pristine || submitting} onClick={() => {
            window.scrollTo(
              {
                top: 0,
                behavior: "smooth"
              }
            )
          }
          }>
            Log in
          </button>
        </div>
      </>
    </form>
  );
};

export default reduxForm({
  form: "LoginForm",
  validate
})(LoginForm);
