import React from "react";
import { Route, Switch,withRouter } from "react-router-dom";
import ResetPassword from "./containers/auth/ResetPassword";
import ResetPasswordConfirm from "./containers/auth/ResetPasswordConfirm";
import Home from "./containers/pages/Home";
import Activate from "./containers/auth/Activate";
import AuthMain from "./containers/auth/AuthMain";
import PrivateRoute from "util/PrivateRoute";
import TheLayout from './containers/dashboard/containers/TheLayout'
import PaymentSuccess from "util/PaymentSuccess";
import PaymentCancel from "util/PaymentCancel";
import AuctionIndex from './containers/pages/AuctionIndex';
import ProjectIndex from './containers/pages/ProjectIndex';
import AuctionDetails from "./containers/subpages/auction/AuctionDetails";
import Layout from "util/Layout";
import ProjectDetails from "./containers/subpages/project/ui/ProjectDetails";


const MainApp = () => {
  return (
    <>
      <Switch>
        <Route path="/activate/:uid/:token" component={Activate} />
        <PrivateRoute
          path="/dashboard"
          name="Home"
          render={props => <TheLayout {...props} />}
        />     
        <Route exact path="/auth" component={AuthMain} />
      
       <Route
          path="/auction-products"
          render={props => <AuctionIndex {...props} />}
        />
      <Route
          path="/projects"
          render={props => <ProjectIndex {...props} />}
        />
        <Route exact path="/reset-password" component={ResetPassword} />
        <Route exact path="/payment-success/" component={PaymentSuccess} />
        <Route exact path="/payment-cancel/" component={PaymentCancel} />
        <Route
          path="/password/reset/confirm/:uid/:token"
          component={ResetPasswordConfirm}
        />
        <Route exact path="/" component={Home} />
        <Layout>
      <Route path="/auction/:id/details" component={AuctionDetails} />
      <Route path="/project/:id/details" component={ProjectDetails} />
      </Layout>
      </Switch>
    </>
  );
};

export default withRouter(MainApp);

   
     