import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { donateToCash } from "store/actions/Projects";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Button from "@material-ui/core/Button";
import { useMediaQuery } from "react-responsive";

const DonateCashForm = ({ project, setCurrentOpenForm, setIsDonateCardButtonsOpen }) => {
  const paystackUrl = useSelector((state) => state.projectsReducer.paystackUrl);

  useEffect(() => {
    if (paystackUrl.length >= 1) {
      window.location = paystackUrl;
    }
  }, [paystackUrl]);


  const [paystack, setPaystack] = useState(false);
  // const [Stripebtn, setStripebtn] = useState(false);

  const [donateFields, setDonateFields] = useState({
    donor_first_name: "",
    donor_last_name: "",
    donor_email: "",
    donor_address: "",
    donor_phone: "",
    company_designation: "",
    company_staff: "",
    donation_amount: "",
    donor_message: "",
    donate_as_unknown: "",
    attestation: ""
  });

  const setPaystackBtn = () => {
    setPaystack(true);
  };

  // const setStripeBtn = () => {
  //   setPaystack(false);
  //   setStripebtn(true);
  // };

  const isDesktopOrLaptop = useMediaQuery({ query: "(min-width: 768px)" });
  const isTabletOrMobile = useMediaQuery({ query: "(max-width: 767px)" });

  const dispatch = useDispatch();

  const {
    donor_first_name,
    donor_last_name,
    donor_email,
    donor_address,
    donor_phone,
    donation_amount,
    donor_message,
    donate_as_unknown,
    donate_payment_method,
    attestation,
  } = donateFields;

  const handleSubmit = (e) => {
    e.preventDefault();
    const formData = {
      donation_amount,
      donate_payment_method,
      donor_first_name,
      donor_last_name,
      donor_email,
      donor_address,
      donor_phone,
      donor_message,
      donate_as_unknown,
      attestation,
      project: project,
    };
    dispatch(donateToCash(formData));
  };

  const handleChange = (e) => {
    setDonateFields({ ...donateFields, [e.target.name]: e.target.value });
  };

  // const ToggleSwitch = ({ checked, onChange, id, name }) => (
  //   <div>
  //     <input
  //       name={name}
  //       type="checkbox"
  //       checked={checked}
  //       onChange={(e) => {
  //         onChange(e.target.checked);
  //         setGiveOgadonate(!percentage);
  //       }}
  //       id={id}
  //     />
  //   </div>
  // );

  const [activeStep, setActiveStep] = useState(0);
  const getSteps = () => {
    return ["User Info", "Donate", "Message", "Attestation"];
  };

  const steps = getSteps();

  const getStepContent = (stepIndex) => {
    switch (stepIndex) {
      case 0:
        return getUserInformation();
      case 1:
        return getAmount();
      case 2:
        return getMessage();
      case 3:
        return getAttestation();
      // case 4:
      //   return getAttestation();
      default:
        return "Uknown stepIndex";
    }
  };

  // you can call this function anything
  const getAttestation = () => {
    return (
      <>
        <label className="mt-3 text-uppercase font-weight-bold">
          Please indicate if you want to donate anonymously
        </label>
        <div className="d-flex">
          <input
            type="checkbox"
            id="anonymous_check"
            name="donate_as_unknown"
            className="mr-3 mt-1"
            defaultChecked={false}
            onChange={handleChange}
          />
          <label className="mt-2"> Yes, I want to donate anonymously </label>
        </div>
        <h2 className="fs-title font-weight-bold text-uppercase mt-3"> Attestation </h2>
        <div className="d-flex">
          <input
            name="attestation"
            type="checkbox"
            required
            className="mr-3 mt-4"
            defaultChecked={false}
            onChange={handleChange}
          />
          <p className="attest">
            I attest that this donation is willful and I am not being forced into giving
          </p>
        </div>
      </>
    );
  };

  const getUserInformation = () => {
    return (
      <fieldset>
        <div className="d-flex mt-2 justify-content-between">
          <div className="d-block">
            <h2 className="fs-title">
              <b>First Name</b>
              <span className="text-danger">*</span>
            </h2>
            <input
              id="donor_first_name"
              name="donor_first_name"
              required="required"
              type="text"
              className="input-number all-input-fields"
              data-rule-required="true"
              data-msg-required="Please enter a valid number"
              onChange={handleChange}
              value={donor_first_name}
            />
          </div>
          <div className="d-block">
            <h2 className="fs-title">
              <b>Last Name</b>
              <span className="text-danger">*</span>
            </h2>
            <input
              id="donor_last_name"
              name="donor_last_name"
              required="required"
              type="text"
              className="input-number all-input-fields"
              data-rule-required="true"
              data-msg-required="Please enter a valid number"
              onChange={handleChange}
              value={donor_last_name}
            />
          </div>
        </div>
        <div className="d-flex mt-4 justify-content-between" >
          <div className="d-block">
          <h2 className="fs-title">
              <b>Email</b>
              <span className="text-danger">*</span>
            </h2>
          <input
            id="donor_email"
            name="donor_email"
            required="required"
            type="text"
            className="input-number all-input-fields"
            data-rule-required="true"
            data-msg-required="Please enter a valid number"
            onChange={handleChange}
            value={donor_email}
          />
          </div>
          <div className="d-block">
          <h2 className="fs-title">
              <b>Phone</b>
              <span className="text-danger">*</span>
            </h2>
          <input
            id="donor_phone"
            name="donor_phone"
            required="required"
            type="text"
            className="input-number all-input-fields"
            data-rule-required="true"
            data-msg-required="Please enter a valid number"
            onChange={handleChange}
            value={donor_phone}
          />
        </div>
        </div>
      </fieldset>
    );
  };
  const getAmount = () => {
    return (
      <fieldset>
        <h2 className="fs-title">
          <b>How much do you want to donate</b>
          <span className="text-danger">*</span>
        </h2>
        <input
          id="amount"
          name="donation_amount"
          required="required"
          type="number"
          className="input-number all-input-fields"
          placeholder="Amount"
          data-rule-required="true"
          data-msg-required="Please enter a valid number"
          onChange={handleChange}
          value={donation_amount}
        />
          <h2 className="fs-title mt-3 font-weight-bold ">Payment Gateway</h2>
          <div className="pay-input-field d-flex">
            <input
              name="donate_payment_method"
              type="radio"
              className="mr-1 ml-2 mt-2"
              onChange={handleChange}
              value="PayStack"
              onClick={setPaystackBtn}
            />
            <p className="mt-2">PayStack</p>
            {/* <input
              name="donate_payment_method"
              type="radio"
              className="mr-1 ml-3 mt-2"
              onChange={handleChange}
              value="Stripe"
              onClick={setStripeBtn}
            />
            <p className="mt-2">Stripe</p> */}
          </div>
      </fieldset>
    );
  };

  const getMessage = () => {
    return (
      <fieldset>
        <label className="font-weight-bold text-uppercase"> Message</label>
        <textarea
          className="donate-message-comment all-input-fields"
          col="50"
          row="50"
          onChange={handleChange}
          placeholder="write a Message or Comment to the fund Raiser"
          name="donor_message"
          value={donor_message}
        />
      </fieldset>
    );
  };

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };
  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <>
      <div className="fundforms_container">
        <form className="w-80">
          <div>
            {isDesktopOrLaptop && (
              <div>
                <Stepper
                  activeStep={activeStep}
                  alternativeLabel
                  className="horizontal-stepper-linear"
                >
                  {steps.map((label, index) => {
                    return (
                      <Step
                        key={label}
                        className={`horizontal-stepper ${index === activeStep ? "active" : ""}`}
                      >
                        <StepLabel className="stepperlabel">{label}</StepLabel>
                      </Step>
                    );
                  })}
                </Stepper>
              </div>
            )}

            {isTabletOrMobile && (
              <>
                <div style={{ marginTop: 20, textAlign: "center" }}>
                  <span>{steps[activeStep]}</span>
                </div>
                <Stepper activeStep={activeStep} style={{ padding: "24px 0px 24px 0px" }}>
                  {steps.map((label, index) => {
                    const props = {};
                    return (
                      <Step style={{ width: 24, padding: 0 }} key={label} {...props}>
                        {/* <StepLabel {...labelProps}></StepLabel> */}
                      </Step>
                    );
                  })}
                </Stepper>
              </>
            )}
          </div>

          {activeStep !== steps.length ? (
            <div>
              {getStepContent(activeStep)}
              <div className="mt-4 flex-wrap d-flex justify-content-between">
                <div>
                  {activeStep !== 0 && (
                    <Button
                      disabled={activeStep === 0}
                      onClick={handleBack}
                      className="mr-2"
                      color="primary"
                    >
                      Back
                    </Button>
                  )}
                  <Button
                    onClick={() => {
                      setCurrentOpenForm(null);
                      setIsDonateCardButtonsOpen(false);
                    }}
                    className="mr-2 ml-2"
                    color="primary"
                  >
                    Cancel
                  </Button>
                </div>

                <Button className="mr-2" variant="contained" color="primary" onClick={handleNext}>
                  {activeStep === steps.length - 1 ? "Proceed to payment" : "Next"}
                </Button>
              </div>
            </div>
          ) : (
            <>
              {paystack ? (
                <Button
                  onClick={handleSubmit}
                  type="submit"
                  name="submit"
                  color="primary"
                  variant="contained"
                >
                  Donate With Paystack
                </Button>
              ) : // <Button
              //   onClick={handleClick}
              //   type="submit"
              //   name="submit"
              //   color="primary"
              //   variant="contained"
              // >
              //   Donate With Stripe
              // </Button>
              null}

              <Button
                disabled={activeStep === 0}
                onClick={handleBack}
                className="mr-2 float-left"
                color="primary"
              >
                Back
              </Button>
              <Button
                disabled={activeStep === 0}
                onClick={handleReset}
                className="mr-2 float-left"
                color="primary"
              >
                Cancel
              </Button>
            </>
          )}
        </form>
      </div>
    </>
  );
};

export default DonateCashForm;
