import axiosInstance from 'util/api'
import * as actionTypes from './ActionTypes';
import { setLoading,offLoading } from './Common';

export const fetchProjects = () => async (dispatch) => {
    try {
        const res = await axiosInstance.get('project/list');
        dispatch({ type: actionTypes.FETCH_ALL_PROJECT_SUCCESS, payload: res.data })
    } catch (error) {
        dispatch({ type: actionTypes.FETCH_ALL_PROJECT_FAIL })
    }
}
export const fetchSingleCampaign  = (id) => async (dispatch) => {
    try {
        const res = await axiosInstance.get(`project/list/${id}`);
        dispatch({ type: actionTypes.FETCH_SINGLE_PROJECT_SUCCESS, payload: res.data })
    } catch (error) {
        dispatch({ type: actionTypes.FETCH_SINGLE_PROJECT_FAIL })
    }
}


export const donateToCash = (formValues) => async (dispatch, getState) => {
    dispatch(setLoading());
      try {
          const res = await axiosInstance.post("project/donate/cash", {...formValues});
          dispatch({type: actionTypes.CREATE_DONATION_CASH_SUCCESS, payload: res.data});
          dispatch(offLoading());
      } catch (error) {
        dispatch({ type: actionTypes.CREATE_DONATION_CASH_FAIL });
        dispatch(offLoading());
        dispatch({type:actionTypes.SHOW_ERROR_MESSAGE, payload:error})
      }
  }
  


