import * as actionTypes from "store/actions/ActionTypes"

const INITIAL_STATE = {
    auctionCollections: [],
    singleAuctionProduct:[],
    
}


const auction = (state=INITIAL_STATE, action) => {
  switch (action.type) {
    case actionTypes.CREATE_AUCTION_SUCCESS:
      return {
        ...state,
        auctionCollections: action.payload
      };
    case actionTypes.FETCH_AUCTION_SUCCESS:
      return {
        ...state,
        auctionCollections: action.payload
      };
    case actionTypes.FETCH_SINGLE_AUCTION_SUCCESS :
      return {
        ...state,
        singleAuctionProduct: action.payload
      };
    default:
      return state;
  }
}

export default auction;